/* General Styles */
body {
    font-family: 'Poppins', sans-serif;
    margin: 0;
    padding: 0;
    background-color: #eaf4fc; /* Light blue background */
    color: #333; /* Dark text */
  }
  
  /* Navbar */
  .navbar {
    display: flex;
    justify-content: flex-end;
    padding: 20px;
    background-color: #f4f9ff;
  }
  
  .navbar ul {
    list-style: none;
    display: flex;
    gap: 20px;
    margin: 0;
    padding: 0;
  }
  
  .navbar li {
    text-transform: capitalize;
    font-weight: bold;
    color: #333;
    cursor: pointer;
  }
  
  /* Title Section */
  .reading-header {
    text-align: center;
    margin-top: 50px;
  }
  
  .reading-header h1 {
    font-size: 4rem;
    color: #9393E4; /* Purple text */
    text-transform: lowercase;
  }
  
  /* Content Section */
  .reading-content {
    width: 80%;
    max-width: 800px;
    margin: 40px auto;
    background-color: #f4f9ff; /* Slightly lighter blue */
    padding: 20px 30px;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  }
  
  .reading-content p {
    font-size: 1rem;
    line-height: 1.8;
    margin-bottom: 20px;
  }
  
  .reading-content ol {
    margin-top: 20px;
    padding-left: 20px;
    font-size: 1rem;
  }
  
  .reading-content ol li {
    margin-bottom: 10px;
    line-height: 1.6;
  }
  